<template>
  <div>
    <da-panel width="100%" height="auto" :noHead="true" padding-content="tblr" padding="b">
      <da-radio-select class="mar-tb-5" v-model="energyRdbSubitem" :maxNum="5" :option-lists="energySubitemList" label="name" value-key="id"></da-radio-select>

      
      <da-radio-select v-model="product" :maxNum="1" :option-lists="productList" label="name" value-key="id" @change="productChange"></da-radio-select>

      
      <da-radio-select v-model="benchMarking" :maxNum="1" :option-lists="benchMarkingList" label="typeName" value-key="typeName" @change="benchMarkingChange"></da-radio-select>

      <da-time-select v-model="statisticsTime" :time-type.sync="dateType" :show-time="[5,8]" @change="changeTime"></da-time-select>

      <button type="button" class="el-button el-button--primary" @click="loadData">
        <span></span>
      </button>
    </da-panel>

    <da-equal-panel :left-width="null" right-width="460px" height="auto">
      <template slot="left">
        <da-panel width="100%" height="auto" :noHead="true">
          <da-line-chart id="egyCspStatistics" :date-time="statisticsTime" :format="dateFormat" :ny-axis="3" :x-axis-format="xAxisFormat" :data-lists="bq_sspTrendData" :line-data="energyQuotaData" label="queryTargetType" x-axis-key="dataTime" :line-data-attr="lineDataAttr"></da-line-chart>
        </da-panel>
      </template>
      <template slot="right">
        <!-- tdcyComparetable -->
        <da-panel :noHead="true" height="252px" padding="l" padding-content="lrtb">
          <da-table class="analysis-table" :colums="columnData1" :table-list="bq_tableData"></da-table>
        </da-panel>
      </template>
    </da-equal-panel>

    <da-equal-panel :left-width="null" right-width="460px" height="auto">
      <div slot="left">
        <da-panel title="" width="100%" height="auto" min-height="200px" padding="t" ref="panelCount">
          <!-- <p>
        <da-time-select v-model="calendarDate" :time-type.sync="calendarDateType" :show-time="[5,8]" @inputChange="calendarChangeTime" @radioChange="calendarChangeCont"></da-time-select>
      </p> -->
          <da-calendar ref="calendar" :target-id="targetId" :disabled="!targetId" v-model="calendarTime" excess-attr="excess" @change="calendarChangeTime">
            <template slot="content" slot-scope="scope">
              <p class="text-center">{{scope.data?scope.data.cl:"-"}}</p>
              <p class="text-center">{{scope.data?scope.data.nh:"-"}}</p>
              <p class="text-center">{{scope.data?scope.data.dh:"-"}}</p>
            </template>
          </da-calendar>
        </da-panel>
      </div>
      <div slot="right">
        <da-panel title="" width="100%" height="auto" min-height="200px" padding="tl" ref="panelCount">
          <da-pie-chart id="egyCspStructure" title="" :data-lists="egyCspStructureData"></da-pie-chart>
        </da-panel>
        <da-panel title="/" width="100%" height="auto" min-height="245px" padding="tl" padding-content="ltbr" ref="panelCount">
          <da-table class="analysis-table" :colums="columnData2" :table-list="tableData"></da-table>
        </da-panel>
      </div>
    </da-equal-panel>

  </div>
</template>
<script>
import {
  deepJSONCopy,
  reduceById,
  calcTime,
  cycleTime,
  getFixedDisplayData
} from "../../../util/util";
import daCalendar from "@/components/base/calendar.vue";
export default {
  components: {
    daCalendar
  },
  data() {
    return {
      targetConstant: this.$store.state.targetConstant,
      energyRdbSubitem: "",
      energySubitemList: [],
      dateType: 5, //
      product: "",
      productList: [],
      orgId: "",
      //calendarDateType: 5, //
      benchMarking: "", //
      benchMarkingList: [], //
      statisticsTime: "",
      startTime: new Date(), //
      endTime: new Date(), //
      // calendarDate: "", //
      calendarTime: [], //
      targetId: { targetCycle: 3 },
      bq_sspTrendData: [],
      bq_data: [],
      tb_data: [],
      hb_data: [],
      bq_tableData: [],
      tableData: [],
      energyQuotaData: {}, //
      egyCspStructureData: [],
      compareTable: {},
      dateFormat: "YYYY-MM", //
      xAxisFormat: "DD", //X
      legendFormat: "YYYYMM", //
      lineDataAttr: [
        { name: "", yIndex: 0, attr: "quota", color: "#FF0000" },
        { name: "", yIndex: 0, attr: "benchmark", color: "#00BB00" }
      ],
      // 
      columnData1: [
        {
          label: "",
          prop: "name"
        },
        {
          label: "",
          prop: "value"
        }
      ], // 
      columnData2: [
        {
          label: "",
          prop: "name"
        },
        {
          label: "",
          prop: "bqValue"
        },
        {
          label: "",
          prop: "tbValue"
        },
        {
          label: "",
          prop: "hbValue"
        }
      ]
    };
  },
  methods: {
    //
    inintData() {
      this.dateType = 5; //

      let t1 = new Date();
      t1 = this.$moment(t1, "YYYY-MM");
      this.statisticsTime = calcTime(t1, -12, 5, "YYYY-MM"); //

      // this.calendarDateType = 5; //
      // this.calendarDate = calcTime(t1, -12, 5, "YYYY-MM"); //

      this.$ajax
        .all([this.getEnergySubitemList(), this.getOrgTreeData()])
        .then(res => {
          if (res[0].data.status) {
            this.energySubitemList = res[0].data.records;
            this.energyRdbSubitem = 35;
          }

          if (res[1].data.status) {
            for (let index = 0; index < res[1].data.records.length; index++) {
              if (res[1].data.records[index].type == 0) {
                this.orgId = res[1].data.records[index].id;
                break;
              }
            }
          }

          this.$nextTick(() => {
            this.getProductList();
          });
        });
    },
    //
    loadData() {
      let time = this.statisticsTime.split("-");
      this.$refs.calendar.render(time[0], parseInt(time[1]) - 1);
      let obj = { 5: 3, 8: 5 };
      this.targetId.targetCycle = obj[this.dateType];

      this.egyCspStructureData = [];

      this.getStartEndTime(this.statisticsTime);
      let params = this.getQueryDataParams(); //
      this.getSspTrendData(params); //
      this.getBenchMarkingList(params); //
      this.getTableData(params, ""); //
      this.getegyCspStructureData(params); //
    },
    /**
     * 
     */
    getEnergySubitemList() {
      let me = this;
      return me.$ajax({
        url: "rest/analysisData/energy",
        method: "get",
        params: { title: "" }
      });
    },
    /**
     * 
     */
    getOrgTreeData() {
      let me = this;
      return me.$ajax({
        url: "rest/analysisData/departTree",
        method: "get",
        params: { title: "" }
      });
    },
    //
    getProductList() {
      let me = this;
      let paramsObj = {
        title: "",
        orgIds: me.orgId //ID
      };
      me
        .$ajax({
          url: "rest/analysisData/product",
          method: "get",
          params: paramsObj
        })
        .then(res => {
          if (res.data.status) {
            me.productList = res.data.records;
            if (res.data.records != null && res.data.records.length > 0) {
              me.product = res.data.records[0].id; //

              let time = this.statisticsTime.split("-");
              this.$refs.calendar.render(time[0], parseInt(time[1]) - 1);

              me.loadData();
            }
          } else {
            this.$message.warning(res.data.answerMsg);
          }
        });
    },
    /**
     * 
     */
    getQueryDataParams() {
      let me = this;
      let cycle = "";
      if (me.dateType == 5) {
        cycle = 3;
        me.dateFormat = "YYYY-MM";
        me.xAxisFormat = "DD";
        me.legendFormat = "YYYYMM";
      } else if (me.dateType == 8) {
        cycle = 5;
        me.dateFormat = "YYYY";
        me.xAxisFormat = "MM";
        me.legendFormat = "YYYY";
      }

      let params = {
        title: "",
        orgIds: me.orgId, //ID
        targetCycle: cycle, //
        targetTypes: "", //
        contentTypes: "", //
        contentIds: me.energyRdbSubitem, //IDID
        objTypes: "", //
        objTypeValues: "", //
        isQueryAll: false, // Boolean,
        // benchmarkQuota: "100", //[][][]
        startTime: "", //
        endTime: "", //
        isOrgId: false, // Boolean,ID
        isContentType: false, // Boolean,ID
        isObjType: "" // String,,123
      };

      return params;
    },
    //
    getBenchMarkingList(params) {
      let me = this;
      let curParam = deepJSONCopy(params);
      curParam.title = "";
      curParam.startTime = me.startTime;
      curParam.endTime = me.endTime;
      curParam.targetTypes =
        me.targetConstant.targetType.unitProductEnergyConsum;

      curParam.contentTypes = me.targetConstant.targetContentType.energy;
      curParam.benchmarkQuota = "110"; //[][][]
      curParam.objTypes = me.targetConstant.targetSubordinate.product;
      curParam.objTypeValues = me.product;
      me
        .$ajax({
          url: "rest/analysisData/targetBenchmarkData",
          method: "get",
          params: curParam
        })
        .then(res => {
          if (res.data.status) {
            me.energyQuotaData = res.data.records;
            me.benchMarkingList = res.data.records.benchmark;
          } else {
            this.$message.warning(res.data.answerMsg);
          }
        });
    },
    //
    getStartEndTime(date) {
      let times = [];
      if (this.dateType == 5) {
        times = cycleTime(date, 5);
      } else if (this.dateType == 8) {
        times = cycleTime(date, 8);
      }
      this.startTime = times[0];
      this.endTime = times[1];
    },
    //
    changeTime(date, type) {
      // this.calendarDate = date;
      // this.calendarDateType = type;
    },

    //
    productChange() {
      this.getStartEndTime(this.statisticsTime);
      let params = this.getQueryDataParams(); //

      this.getBenchMarkingList(params); //
    },
    //
    benchMarkingChange() {
      let benchmark = [];
      for (let i = 0; i < this.benchMarkingList.length; i++) {
        if (this.benchMarkingList[i].typeName == value) {
          benchmark[0] = this.benchMarkingList[i];
          this.energyQuotaData.benchmark = benchmark;
        }
      }
    },

    //
    getSspTrendData(params) {
      let me = this;
      let curParam = deepJSONCopy(params);
      curParam.title = "";
      curParam.startTime = this.startTime;
      curParam.endTime = this.endTime;
      curParam.targetTypes =
        me.targetConstant.targetType.energyConsum +
        "," +
        me.targetConstant.targetType.harvest +
        "," +
        me.targetConstant.targetType.unitProductEnergyConsum;
      curParam.contentTypes =
        me.targetConstant.targetContentType.energy +
        ",," +
        me.targetConstant.targetContentType.energy;
      curParam.contentIds = me.energyRdbSubitem + "##" + me.energyRdbSubitem;
      curParam.objTypes = me.targetConstant.targetSubordinate.product;
      curParam.objTypeValues = me.product;
      me
        .$ajax({
          url: "rest/analysisData/targetData",
          method: "get",
          params: curParam
        })
        .then(res => {
          if (res.data.status) {
            // me.calendarChangeData(res.data.records);
            me.bq_sspTrendData = res.data.records;
            let arr = [];
            let queryTargetType = [2, 1, 4]; //Y
            for (let j = 0; j < queryTargetType.length; j++) {
              for (let i = 0; i < me.bq_sspTrendData.length; i++) {
                if (
                  me.bq_sspTrendData[i].queryTargetType == queryTargetType[j]
                ) {
                  if (me.bq_sspTrendData[i].queryTargetType == 4) {
                    me.bq_sspTrendData[i].queryTargetType = "";
                  } else if (me.bq_sspTrendData[i].queryTargetType == 2) {
                    me.bq_sspTrendData[i].queryTargetType = "";
                  } else if (me.bq_sspTrendData[i].queryTargetType == 1) {
                    me.bq_sspTrendData[i].queryTargetType = "";
                  }
                  arr = arr.concat(me.bq_sspTrendData[i]);
                }
              }
            }
            me.bq_sspTrendData = arr;
            getFixedDisplayData(me.bq_sspTrendData);
          } else {
            this.$message.warning(res.data.answerMsg);
          }
        });
    },
    //
    getTableData(params, type) {
      let me = this;
      let curParam = deepJSONCopy(params);
      curParam.title = type + "";
      curParam.startTime = this.startTime;
      curParam.endTime = this.endTime;
      curParam.targetCycle = me.dateType;
      curParam.targetTypes =
        me.targetConstant.targetType.energyConsum +
        "," +
        me.targetConstant.targetType.harvest +
        "," +
        me.targetConstant.targetType.unitProductEnergyConsum;
      curParam.contentTypes =
        me.targetConstant.targetContentType.energy +
        ",," +
        me.targetConstant.targetContentType.energy;
      curParam.contentIds = me.energyRdbSubitem + "##" + me.energyRdbSubitem;
      curParam.objTypes = me.targetConstant.targetSubordinate.product;
      curParam.objTypeValues = me.product;
      me
        .$ajax({
          url: "rest/analysisData/targetData",
          method: "get",
          params: curParam
        })
        .then(res => {
          if (res.data.status) {
            if (type == "") {
              me.bq_data = res.data.records;
              this.bq_tableData = [];
              for (let i = 0; i < me.bq_data.length; i++) {
                if (me.bq_data[i].queryTargetType == 4) {
                  this.bq_tableData.push({
                    name: "",
                    value:
                      me.bq_data[i].dataValue.toFixed(2) +
                      " " +
                      me.bq_data[i].targetUnit
                  });
                } else if (me.bq_data[i].queryTargetType == 2) {
                  this.bq_tableData.push({
                    name: "",
                    value:
                      me.bq_data[i].dataValue.toFixed(2) +
                      " " +
                      me.bq_data[i].targetUnit
                  });
                }
                if (me.bq_data[i].queryTargetType == 1) {
                  this.bq_tableData.push({
                    name: "",
                    value:
                      me.bq_data[i].dataValue.toFixed(2) +
                      " " +
                      me.bq_data[i].targetUnit
                  });
                }
              }

              me.getStartEndTime(this.getTime(2));
              me.getTableData(params, "");
            } else if (type == "") {
              me.tb_data = res.data.records;
              me.getStartEndTime(this.getTime(1));
              me.getTableData(params, "");
            } else if (type == "") {
              me.hb_data = res.data.records;
              let bqValue = "";
              let tbValue = "";
              let hbValue = "";
              this.tableData = [];
              for (let i = 0; i < me.bq_data.length; i++) {
                if (me.bq_data[i].queryTargetType == 4) {
                  bqValue = me.bq_data[i].dataValue.toFixed(2);
                  for (let j = 0; j < me.tb_data.length; j++) {
                    if (me.tb_data[j].queryTargetType == 4) {
                      tbValue = me.tb_data[j].dataValue.toFixed(2);
                    }
                  }
                  for (let j = 0; j < me.hb_data.length; j++) {
                    if (me.hb_data[j].queryTargetType == 4) {
                      hbValue = me.hb_data[j].dataValue.toFixed(2);
                    }
                  }
                  this.tableData.push({
                    name: "(" + me.bq_data[i].targetUnit + ")",
                    bqValue: bqValue,
                    tbValue:
                      tbValue +
                      (tbValue != 0
                        ? " (" +
                          ((bqValue - tbValue) / tbValue * 100).toFixed(2) +
                          " % "
                        : "--"),
                    hbValue:
                      hbValue +
                      (hbValue != 0
                        ? " (" +
                          ((bqValue - hbValue) / hbValue * 100).toFixed(2) +
                          " % "
                        : "--")
                  });
                } else if (me.bq_data[i].queryTargetType == 2) {
                  bqValue = me.bq_data[i].dataValue.toFixed(2);
                  for (let j = 0; j < me.tb_data.length; j++) {
                    if (me.tb_data[j].queryTargetType == 2) {
                      tbValue = me.tb_data[j].dataValue.toFixed(2);
                    }
                  }
                  for (let j = 0; j < me.hb_data.length; j++) {
                    if (me.hb_data[j].queryTargetType == 2) {
                      hbValue = me.hb_data[j].dataValue.toFixed(2);
                    }
                  }
                  this.tableData.push({
                    name: "(" + me.bq_data[i].targetUnit + ")",
                    bqValue: bqValue,
                    tbValue:
                      tbValue +
                      (tbValue != 0
                        ? " (" +
                          ((bqValue - tbValue) / tbValue * 100).toFixed(2) +
                          " % "
                        : "--"),
                    hbValue:
                      hbValue +
                      (hbValue != 0
                        ? " (" +
                          ((bqValue - hbValue) / hbValue * 100).toFixed(2) +
                          " % "
                        : "--")
                  });
                }
                if (me.bq_data[i].queryTargetType == 1) {
                  bqValue = me.bq_data[i].dataValue.toFixed(2);
                  for (let j = 0; j < me.tb_data.length; j++) {
                    if (me.tb_data[j].queryTargetType == 1) {
                      tbValue = me.tb_data[j].dataValue.toFixed(2);
                    }
                  }
                  for (let j = 0; j < me.hb_data.length; j++) {
                    if (me.hb_data[j].queryTargetType == 1) {
                      hbValue = me.hb_data[j].dataValue.toFixed(2);
                    }
                  }
                  this.tableData.push({
                    name: "(" + me.bq_data[i].targetUnit + ")",
                    bqValue: bqValue,
                    tbValue:
                      tbValue +
                      (tbValue != 0
                        ? " (" +
                          ((bqValue - tbValue) / tbValue * 100).toFixed(2) +
                          " % "
                        : "--"),
                    hbValue:
                      hbValue +
                      (hbValue != 0
                        ? " (" +
                          ((bqValue - hbValue) / hbValue * 100).toFixed(2) +
                          " % "
                        : "--")
                  });
                }
              }
            }
          } else {
            this.$message.warning(res.data.answerMsg);
          }
        });
    },
    //
    getegyCspStructureData(params) {
      let me = this;
      let curParam = deepJSONCopy(params);
      curParam.title = "";
      curParam.startTime = this.startTime;
      curParam.endTime = this.endTime;
      curParam.targetCycle = me.dateType;
      curParam.targetTypes = me.targetConstant.targetType.energyConsum;
      curParam.contentTypes = "";
      curParam.contentIds = "";
      curParam.objTypes = me.targetConstant.targetSubordinate.product;
      curParam.objTypeValues = me.product;
      curParam.isContentType = true;
      me
        .$ajax({
          url: "rest/analysisData/targetData",
          method: "get",
          params: curParam
        })
        .then(res => {
          if (res.data.status) {
            for (let i = 0; i < res.data.records.length; i++) {
              if (res.data.records[i].targetType != 23) {
                //
                me.egyCspStructureData = me.egyCspStructureData.concat(
                  res.data.records[i]
                );
                //
                for (let j = 0; j < this.energySubitemList.length; j++) {
                  if (
                    res.data.records[i].contentId ==
                    this.energySubitemList[j].id
                  ) {
                    res.data.records[i].name = this.energySubitemList[j].name;
                  }
                }
              }
            }
            getFixedDisplayData(me.egyCspStructureData);
          } else {
            this.$message.warning(res.data.answerMsg);
          }
        });
    },
    //
    getTime(type) {
      let time = "";
      if (type == 1) {
        //
        if (this.dateType == 5) {
          time = calcTime(
            this.$moment(this.statisticsTime, "YYYY-MM"),
            -1,
            5,
            "YYYY-MM"
          ); //
        } else if (this.dateType == 8) {
          time = calcTime(
            this.$moment(this.statisticsTime, "YYYY"),
            -1,
            8,
            "YYYY"
          ); //
        }
      } else if (type == 2) {
        //
        if (this.dateType == 5) {
          time = calcTime(
            this.$moment(this.statisticsTime, "YYYY-MM"),
            -1,
            8,
            "YYYY-MM"
          ); //
        } else if (this.dateType == 8) {
          time = calcTime(
            this.$moment(this.statisticsTime, "YYYY"),
            -1,
            8,
            "YYYY"
          ); //
        }
      }
      return time;
    },
    //
    calendarChangeData(data) {
      let me = this;
      let calendarData = [];
      let cl = 0;
      let nh = 0;
      let dh = 0;
      for (let i = 0; i < data.length; i++) {
        if (data[i].flag1 != 1) {
          //data[i].flag = 1;
          if (data[i].queryTargetType == 4) {
            cl = data[i].dataValue.toFixed(2);
          } else if (data[i].queryTargetType == 2) {
            dh = data[i].dataValue.toFixed(2);
          } else if (data[i].queryTargetType == 1) {
            nh = data[i].dataValue.toFixed(2);
          }
          for (let j = 0; j < data.length; j++) {
            if (data[j].flag1 != 1 && data[i].time == data[j].time) {
              data[j].flag1 = 1;
              if (data[j].queryTargetType == 4) {
                cl = data[j].dataValue.toFixed(2);
              } else if (data[j].queryTargetType == 2) {
                dh = data[j].dataValue.toFixed(2);
              } else if (data[j].queryTargetType == 1) {
                nh = data[j].dataValue.toFixed(2);
              }
            }
          }
          calendarData.push({
            cl: cl,
            nh: nh,
            dh: dh,
            time: data[i].time,
            excess: false
          });
        }
      }

      this.$refs.calendar.setInputs(calendarData, "cl", "time");
    },
    //
    calendarChangeTime() {
      this.getStartEndTime(this.calendarTime[0]);
      let params = this.getQueryDataParams(); //
      this.getproductionCalendarData(params); //
    },

    //
    getproductionCalendarData(params) {
      let me = this;
      let curParam = deepJSONCopy(params);
      curParam.title = "";
      curParam.startTime = this.startTime;
      curParam.endTime = this.endTime;
      curParam.targetTypes =
        me.targetConstant.targetType.energyConsum +
        "," +
        me.targetConstant.targetType.harvest +
        "," +
        me.targetConstant.targetType.unitProductEnergyConsum;
      curParam.contentTypes =
        me.targetConstant.targetContentType.energy +
        ",," +
        me.targetConstant.targetContentType.energy;
      curParam.contentIds = me.energyRdbSubitem + "##" + me.energyRdbSubitem;
      curParam.objTypes = me.targetConstant.targetSubordinate.product;
      curParam.objTypeValues = me.product;
      me
        .$ajax({
          url: "rest/analysisData/targetData",
          method: "get",
          params: curParam
        })
        .then(res => {
          console.log(res.data);
          if (res.data.status) {
            me.calendarChangeData(res.data.records);
          } else {
            this.$message.warning(res.data.answerMsg);
          }
        });
    }
  },
  mounted() {
    this.inintData();
  }
};
</script>




